@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap');

body {
  font-family: 'Montserrat', sans-serif;
  background-color: #ffffff;
  color: #4a4a4a;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h1, h2 {
  color: #4a4a4a;
}

.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #d3d3d3;
  border-radius: 10px;
  background-color: #ffffff;
}

.form-label {
  font-weight: bold;
}

.status-open {
  background-color: #28a745;
  color: #ffffff;
  padding: 5px 10px;
  border-radius: 27px;
}

.status-closed {
  background-color: #dc3545;
  color: #ffffff;
  padding: 5px 10px;
  border-radius: 27px;
}

.copy-button {
  margin-left: 10px;
}

button {
  margin: 5px 0;
}

.table-container {
  overflow-x: auto;
  margin-top: 20px;
}

.table {
  border-collapse: collapse;
  width: 100%;
  margin: 0 auto;
  border: 1px solid #d3d3d3;
  border-radius: 10px;
}

.table th, .table td {
  border: none;
  padding: 15px;
  text-align: center;
  vertical-align: middle;
}

.table th {
  background-color: #f8f9fa;
  font-weight: 700;
}

.table td {
  background-color: #ffffff;
  border-top: 1px solid #d3d3d3;
}

.table tr:nth-child(even) td {
  background-color: #f2f2f2;
}

.table tr:last-child td {
  border-bottom: none;
}

.navbar {
  background-color: #4a4a4a;
  padding: 1rem;
}

.navbar ul {
  list-style: none;
  display: flex;
  justify-content: space-around;
  padding: 0;
  margin: 0;
}

.navbar ul li {
  margin: 0 1rem;
}

.navbar ul li a {
  color: white;
  text-decoration: none;
  font-size: 1.2rem;
}

.navbar ul li a:hover {
  text-decoration: underline;
}

.btn {
  border-radius: 15px;
  font-family: 'Montserrat', sans-serif;
}

.btn-primary {
  background-color: #ff5e01;
  border-color: #ff5e01;
}

.btn-primary:hover {
  background-color: #e04e00;
  border-color: #e04e00;
}

.btn-secondary {
  background-color: #6c757d;
  border-color: #6c757d;
}

.btn-secondary:hover {
  background-color: #5a6268;
  border-color: #545b62;
}

.btn-outline-secondary {
  border-color: #6c757d;
  color: #6c757d;
}

.btn-outline-secondary:hover {
  background-color: #f8f9fa;
}

.btn-success {
  background-color: #28a745;
  border-color: #28a745;
}

.btn-success:hover {
  background-color: #218838;
  border-color: #1e7e34;
}

.btn-danger {
  background-color: #dc3545;
  border-color: #dc3545;
}

.btn-danger:hover {
  background-color: #c82333;
  border-color: #bd2130;
}

.btn-warning {
  background-color: #ffc107;
  border-color: #ffc107;
}

.btn-warning:hover {
  background-color: #e0a800;
  border-color: #d39e00;
}

.action-buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

.pagination {
  display: flex;
  justify-content: center;
  padding: 0;
  list-style: none;
  margin-top: 20px;
}

.page-item {
  margin: 0 5px;
}

.page-link {
  color: #007bff;
  text-decoration: none;
  padding: 8px 12px;
  border: 1px solid #dee2e6;
  border-radius: 4px;
}

.page-link:hover {
  background-color: #e9ecef;
  color: #0056b3;
  border-color: #dee2e6;
}

.claims-dashboard-container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #d3d3d3;
  border-radius: 10px;
  background-color: #ffffff;
  overflow: hidden;
}

.claims-dashboard-container .table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
  table-layout: fixed;
}

.claims-dashboard-container .table th, 
.claims-dashboard-container .table td {
  padding: 15px;
  text-align: center;
  vertical-align: middle;
  border-top: 1px solid #d3d3d3;
  word-wrap: break-word;
}

.claims-dashboard-container .table th {
  background-color: #f8f9fa;
  font-weight: 700;
}

.claims-dashboard-container .table tr:nth-child(even) td {
  background-color: #f2f2f2;
}

.statistics-overview {
  display: flex;
  justify-content: space-around;
  margin-top: 20px;
  flex-wrap: wrap;
}

.stat-container {
  background-color: #f8f9fa;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
  width: 23%;
  margin-bottom: 20px;
  box-sizing: border-box;
}

.filter-options {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: 20px;
}

.stats-details {
  display: flex;
  justify-content: space-between;
  margin-top: 40px;
  flex-wrap: wrap;
}

.chart-container, .top-customers, .pie-chart-container {
  flex: 1;
  margin: 10px;
  min-width: 300px;
}

.chart-container, .pie-chart-container {
  max-width: 60%;
}

.top-customers ul {
  list-style-type: none;
  padding: 0;
}

@media (max-width: 768px) {
  .chart-container, .top-customers, .pie-chart-container {
    max-width: 100%;
    flex: 1 1 100%;
  }
}

.notes-table {
  margin-top: 20px;
}

.notes-table h2 {
  margin-bottom: 15px;
}

.notes-table .table {
  width: 100%;
  border-collapse: collapse;
}

.notes-table .table th, 
.notes-table .table td {
  border: 1px solid #ddd;
  padding: 8px;
}

.notes-table .table th {
  padding-top: 12px;
  padding-bottom: 12px;
  background-color: #f8f9fa;
  color: #333;
  text-align: left;
}

.notes-table .table td {
  text-align: left;
  vertical-align: top;
}

.notes-table .text-muted {
  color: #6c757d;
}

.login-container {
  width: 400px;
  margin: 100px auto;
  padding: 30px;
  border: 1px solid #ccc;
  border-radius: 10px;
  background-color: #f9f9f9;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.login-container h1 {
  font-size: 24px;
  margin-bottom: 20px;
}

.login-container p {
  margin-bottom: 10px;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  font-weight: bold;
  margin-bottom: 5px;
}

.form-group input {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.btn {
  width: 100%;
  padding: 10px;
  border: none;
  border-radius: 5px;
  background-color: #ff5e01;
  color: white;
  font-size: 16px;
  cursor: pointer;
}

.btn:hover {
  background-color: #e04e00;
}

.error-text {
  color: red;
  margin-bottom: 10px;
}

